import styles from './index.module.css'
import React, { useEffect, useState } from 'react'

function Crossfade({ images, customInterval }) {
  const defaultImages = [
    'https://images.unsplash.com/photo-1441974231531-c6227db76b6e?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1351&q=80',
    'https://images.unsplash.com/photo-1470071459604-3b5ec3a7fe05?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1440&q=80',
    'https://images.unsplash.com/photo-1470770903676-69b98201ea1c?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1350&q=80'
  ]
  const backgroundImages = images || defaultImages
  const carouselLength = backgroundImages.length
  const [currentCarouselIndex, setCarouselIndex] = useState(0)

  useEffect(() => {
    const interval = setInterval(() => {
      setCarouselIndex((currentCarouselIndex) => {
        const newIndex =
          currentCarouselIndex + 1 >= carouselLength
            ? 0
            : currentCarouselIndex + 1
        return newIndex
      })
    }, customInterval || 5000)
    return () => clearInterval(interval)
  }, [currentCarouselIndex])

  const carousel = backgroundImages.map((element, index) => {
    return (
      <img
        key={index}
        className={`${styles.background} ${
          currentCarouselIndex === index ? styles.active : styles.inactive
        }`}
        src={element}
      />
    )
  })

  return <div className={styles.backgroundBackground}>{carousel}</div>
}

export default Crossfade
